import type { AxiosRequestConfig } from 'axios';
import * as buildFullPath from 'axios/lib/core/buildFullPath';
import * as buildUrl from 'axios/lib/helpers/buildURL';

/**
 * @see https://github.com/axios/axios/pull/3737
 */
export function axiosGetFullUri(config: AxiosRequestConfig): string {
  // config = mergeConfig(this.defaults, config);
  const fullPath = buildFullPath.default(config.baseURL, config.url);
  return buildUrl.default(fullPath, config.params, config.paramsSerializer);
}
